import './NavBar.css'
import logo from '../../Utils/img/logo.svg';
import { ReactComponent as HamBurger } from "../../Utils/img/hamburger.svg";

function NavBar(){
    const handleHamburgerClick = () => {
        var hamburger = document.querySelector(".hamburger-link");
        console.log(hamburger)
        hamburger.classList.toggle('open');
    }

    return(
        <nav className='navbar'>
            <img src={logo} className='logo-design' alt='logo'/>
            <ul className='navbar-items'>
                <li>Home</li>
                <li>Service</li>
                <li>Pricing</li>
                <li>Track Project</li>
                <li>About us</li>
            </ul>
            <button className='intouch-button'>Get in touch</button>


            <div className='hamburger'>
                <HamBurger className='hamburger-icon' onClick={() => handleHamburgerClick()}/>
                <ul className='hamburger-link'>
                    <li>Home</li>
                    <li>Service</li>
                    <li>Pricing</li>
                    <li>Track Project</li>
                    <li>About us</li>  
                    <li>Get in touch</li>  
                </ul>
            </div>
        </nav>
    );
}
export default NavBar;
