import './Service.css'
import {ReactComponent as Education} from '../../Utils/img/education.svg';
import {ReactComponent as WebDesign} from '../../Utils/img/ui-ux-design.svg';
import {ReactComponent as WebDevelopment} from '../../Utils/img/web_development.svg';
import {ReactComponent as Hospital} from '../../Utils/img/hospital.svg';
import {ReactComponent as OnlineStore} from '../../Utils/img/online-store.svg';
import {ReactComponent as RealEstate} from '../../Utils/img/real-estate.svg';
import { useEffect } from 'react';

function Service(){
    useEffect(() => {
        const serviceCards  = document.querySelectorAll('.service-card');
        var index = 1;

        function addAnimation(index, card) {
            card.classList.add('service-card-animation');

            card.addEventListener('animationend', () => {
                console.log("Animation End");
            })
        }

        addAnimation(0, serviceCards[0]);

        var interValId = setInterval(()=>{
            addAnimation(index, serviceCards[index++]);

            if (index === serviceCards.length) {
                clearInterval(interValId);
            }
        }, 6000);

    }, [])
    return(
        <div className='service-container'>
            <h1>Our service</h1 >
            <p>Whether you need a cutting-edge enterprise solution or seamless software integration, The Ingenious Tech transforms your vision into reality with expert IT consulting and customized software development services</p>

            <div className='service-cards'>
                <div className='service-card'>
                    <WebDesign className='svg-style-1'/>
                    <h3>Web Design</h3>
                    <p>description</p>
                </div>

                <div className='service-card'>
                    <WebDevelopment className='svg-style-1' />
                    <h3>Website Development</h3>
                    <p>description</p>
                </div>

                <div className='service-card'>
                    <OnlineStore className='svg-style-1' />
                    <h3>Online Store</h3>
                    <p>description</p>
                </div>

                <div className='service-card'>
                    <Education className='svg-style-3'/>
                    <h3>Education</h3>
                    <p>description</p>
                </div>

                <div className='service-card'>
                    <Hospital className='svg-style-3' />
                    <h3>Hospital</h3>
                    <p>description</p>
                </div>

                <div className='service-card'>
                    <RealEstate className='svg-style-4' />
                    <h3>Real-Estate</h3>
                    <p>description</p>
                </div>
            </div>
        </div>
    );
}
export default Service;
